@import url("https://fonts.googleapis.com/css2?family=Bree+Serif&family=Caveat:wght@500&display=swap");
*
{
  margin:0;
  padding:0;
  font-family: "Verdana", cursive, sans-serif;
  color: black;
}

  body{
    background-image: url('http://superdevresources.com/wp-content/uploads/2014/02/pattern-background.png');
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    width:100%;
  }
  .header
  {
    color: #f71aa2;
    text-align: center;
  }
  .app
  {
    background: linear-gradient(180deg,#f30dcc 0%, #ff8100 80%);
    width: 95%;
    height: 200px;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    display: flex;
    justify-content: space-evenly;
  }
  .score-section{
    color:white;
    font-size: 45px;
  }
  .question-Section
  {
    position: relative;
    font-size: 30px;
    color:white
  }
  
  .question-count{
    margin-bottom: 20px;
   
  }
  .question-count span
  {
    color:crimson;
    font-size: 20px;
  }
.question-text
{
  margin-bottom: 12px;
  color:blue;
}
.answer-section
{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
button {
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  background: linear-gradient(180deg,#f30dcc 0%, #ff8100 80%);
  border-radius: 15px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid #0de24d;
  cursor: pointer;
}

button:hover {
  border-color: #1b295c;
}



  
  
